import Artplayer from 'artplayer';
import mpegts from 'mpegts.js';
import artplayerPluginDanmuku from 'artplayer-plugin-danmuku';

import ploading from "./img/ploading.gif";
import state from "./img/state.png";
import indicator from "./img/indicator.svg";
import filp from "./img/filp.svg";
import http from "stream-http";

(() => {
    class FIFO {
        #ok=false;
        #db;
        #size=0;
        #cu=1;

        constructor(okf) {
            const that = this;
            const indexedDB = window.indexedDB;
            if (!indexedDB) {
                console.error("IndexedDB could not be found in this browser.");
            }

            const DBDeleteRequest = window.indexedDB.deleteDatabase("FIFO");
            DBDeleteRequest.onerror = (event) => {
                console.error("Error deleting database.");
            };
            DBDeleteRequest.onsuccess = (event) => {
                console.log("Database deleted successfully");
            };

            const request = indexedDB.open("FIFO", 1);

            request.onerror = function (event) {
                console.error("An error occurred with IndexedDB");
                console.error(event);
            };
            
            request.onupgradeneeded = function () {
                that.#db = request.result;
                that.#db.createObjectStore("fifo", { keyPath: "id", autoIncrement: true });
            };
            
            request.onsuccess = function () {
                console.log("Database opened successfully");
                that.#db = request.result;
                that.#ok = true;
                if(okf)okf(that);
            };
        }

        #getTx(mode,func) {
            if(!this.#ok)return;
            const transaction = this.#db.transaction("fifo", mode);
            transaction.onerror = (event) => {
                console.error("An error occurred with put");
                console.error(event);
            };
            transaction.oncomplete = function () {};
            return func(transaction, transaction.objectStore("fifo"));
        }

        #stillTx(transaction,func) {
            return func(transaction, transaction.objectStore("fifo"));
        }

        size(){
            return new Promise((resolve) => resolve(this.#size));
        }

        showSize(){
            return this.#getTx("readonly",  (transaction, store)=>{
                const idQuery = store.count();
                idQuery.onsuccess = function () {
                    console.log(this.#size);
                };
            });
        }

        put(data){
            const that = this;
            return this.#getTx("readwrite",  (transaction, store)=>{
                return new Promise((resolve) => {
                    store.put({ data: data });
                    that.#size += 1;
                    resolve(that.#size);
                });
            });
        }

        get(){
            const that = this;
            return this.#getTx("readwrite", (transaction, store)=>{
                return new Promise((resolve, reject) => {
                    const idQuery = store.get(that.#cu);
                    idQuery.onsuccess = async function () {
                        if(idQuery.result){
                            that.#size -= 1;
                            that.#cu += 1;
                            await that.#stillTx(transaction,  (transaction, store)=>{
                                return new Promise((resolve) => {
                                    transaction.oncomplete = function () {
                                        resolve(idQuery.result);
                                    };
                                    store.delete(idQuery.result.id)
                                });
                            });
                            resolve(idQuery.result);
                        } else reject();
                    };
                });
            });
        }

        close(){
            if(this.#ok)this.#db.close();

            const DBDeleteRequest = window.indexedDB.deleteDatabase("FIFO");
            DBDeleteRequest.onerror = (event) => {
                console.error("Error deleting database.");
            };
            DBDeleteRequest.onsuccess = (event) => {
                console.log("Database deleted successfully");
            };
        }
    }

    {
        // new FIFO(async fifo=>{
        //     fifo.put(1).then(size=>size!=1?console.error("size:1 ",size):console.log("1ok"));
        //     fifo.put(2).then(size=>size!=2?console.error("size:2 ",size):console.log("2ok"));
        //     fifo.put(3).then(size=>size!=3?console.error("size:3 ",size):console.log("3ok"));
        //     fifo.put(4).then(size=>size!=4?console.error("size:4 ",size):console.log("4ok"));
        //     fifo.size().then(size=>size!=4?console.error("size:4 ",size):console.log("5ok"));
        //     console.log('1!')
        //     await fifo.get().then(result=>console.log(result));
        //     console.log('2!')
        //     await fifo.get().then(result=>console.log(result));
        //     console.log('3!')
        //     console.log("fin");
        // });
    }

    console.log("init 22");
    let mp4LoadFromDB = 20,
        mp4StopFromDB = 30,
        mp4LoadFromWeb = 1000,
        mp4StopFromWeb = 2000,
        tabUnload = false,
        player,
        flvPlayer,
        danmuEmit = document.createElement("div"),
        config = {
            conn: undefined,
            container: '.artplayer-app',
            url: "../stream?_=" + new Date().getTime()+"&ref="+new URL(window.location.href).searchParams.get("ref"),
            title: "" + new Date().getTime(),
            type: new URL(window.location.href).searchParams.get("format")||"flv",
            volume: 0.5,
            hotkey: false,
            isLive: true,
            muted: false,
            autoplay: true,
            autoMini: true,
            screenshot: true,
            setting: true,
            loop: false,
            flip: true,
            playbackRate: true,
            aspectRatio: true,
            fullscreen: true,
            fullscreenWeb: true,
            subtitleOffset: true,
            miniProgressBar: true,
            mutex: true,
            backdrop: true,
            playsInline: true,
            autoPlayback: true,
            theme: '#23ade5',
            lang: navigator.language.toLowerCase(),
            whitelist: ['*'],
            moreVideoAttr: {
                crossOrigin: 'anonymous',
            },
            settings: [],
            contextmenu: [],
            layers: [],
            quality: [],
            thumbnails: {},
            subtitle: {},
            highlight: [],
            controls: [
                {
                    name: '',
                    index: 10,
                    position: 'right',
                    html: '<img width="22" heigth="22" src="'+ filp +'">',
                    click: function (...args) {
                        let f = function(...e){
                            // if(e)alert(e);
                            rotate(document.querySelector('.art-video'));
                            rotate(document.querySelector('.art-danmuku'));
                        }, rotate = function(element) {
                            if(element.style.transform == 'rotateZ(0deg)' || element.style.transform == ''){
                                element.style.transform = 'rotateZ(180deg)';
                            }
                            else {
                                element.style.transform = 'rotateZ(0deg)';
                            }
                        };

                        switch (screen.orientation.type) {
                            case "landscape-primary":
                                screen.orientation.lock("landscape-secondary").catch(e=>{f(e);});
                                break;
                            case "landscape-secondary":
                                screen.orientation.lock("landscape-primary").catch(e=>{f(e);});
                                break;
                            case "portrait-secondary":
                                screen.orientation.lock("portrait-primary").catch(e=>{f(e);});
                                break;
                            case "portrait-primary":
                                screen.orientation.lock("portrait-secondary").catch(e=>{f(e);});
                                break;
                            default:
                                f();
                        }
                    },
                }
            ],
            plugins: [
                artplayerPluginDanmuku({
                    danmuku: [],
                    speed: 7,
                    opacity: 0.7,
                    mount: danmuEmit,
                }),
            ],
            icons: {
                loading: '<img src=' + ploading + '>',
                state: '<img width="150" heigth="150" src=' + state + '>',
                indicator: '<img width="16" heigth="16" src=' + indicator + '>',
            },
            customType: {
                mp4: function (video, url) {
                    new FIFO(fifo => {
                        var mediaSource = new MediaSource();
                        mediaSource.addEventListener('sourceopen', () => {
                            // Create a new SourceBuffer
                            var sourceBuffer = mediaSource.addSourceBuffer('video/mp4; codecs="avc1.640032,mp4a.40.2"');
                            var mscBuf = ()=>{
                                if(sourceBuffer.buffered.length == 0)return 0;
                                else return sourceBuffer.buffered.end(sourceBuffer.buffered.length-1) - player.currentTime
                            };
    
                            sourceBuffer.mode = "sequence";
    
                            sourceBuffer.addEventListener('error', (event) => {
                                console.error('SourceBuffer error:', event);
                            });
    
                            var runUpdate = false;
                            sourceBuffer.addEventListener("updateend", ()=>{
                                fifo.size().then(size=>{
                                    runUpdate = size > 0 && mscBuf() < mp4StopFromDB;
                                    if(!runUpdate)return;
                                    fifo.get().then(({data})=>sourceBuffer.appendBuffer(data)).catch(() => {});
                                });
                            });
    
                            fetch(url)
                            // Retrieve its body as ReadableStream
                            .then((response) => {
                                const reader = response.body.getReader();
    
                                var fin = false;
                                var loading = false;
                                var loadf = ()=>
                                fifo.size().then(size=>{
                                    if (fin && size==0 && mediaSource.readyState=="open") {
                                        console.log("fin");
                                        mediaSource.endOfStream();
                                        console.log(mediaSource.readyState);
                                        return;
                                    }
                                    if(!sourceBuffer || sourceBuffer.updating)return setTimeout(loadf, 1000);
    
                                    var mscb = mscBuf();
    
                                    if(size >= mp4LoadFromWeb && runUpdate){
                                        console.log("web %s db (%d) %s msc (%d)", loading?">":" ", size, runUpdate?">":" ", mscb);
                                        return setTimeout(loadf, 1000);
                                    }
    
                                    if (!loading && size < mp4StopFromWeb)reader.read().then(pump);
                                    function pump({ done, value }) {
                                        loading = true;
                                        if (done || tabUnload){
                                            loading = false;
                                            fin = true;
                                            return;
                                        }
                                        fifo.put(value);
                                        size+=1;
                                        if (size < mp4StopFromWeb)return reader.read().then(pump);
                                        else loading = false;
                                    }
    
                                    if(mscBuf() < mp4LoadFromDB)
                                        fifo.size().then(size=>{
                                            runUpdate = size > 0 && mscBuf() < mp4StopFromDB;
                                            if(!runUpdate)return;
                                            console.log("web %s db (%d) %s msc (%d)", loading?">":" ", size, runUpdate?">":" ", mscb);
                                            fifo.get().then(({data})=>sourceBuffer.appendBuffer(data)).catch(() => {});
                                        });
                                    else console.log("web %s db (%d) %s msc (%d)", loading?">":" ", size, runUpdate?">":" ", mscb);
                                    return setTimeout(loadf, 1000);
                                });
                                loadf();
                            })
                            .catch((err) => console.error(err));
                        });
                        window.player = video;
                        video.src = URL.createObjectURL(mediaSource);
                        video.pause();
                    });
                },
                flv: function (video, url) {
                    var needUnload = true;
                    if(flvPlayer){
                        needUnload = false;
                        flvPlayer.destroy();
                    }
                    if (mpegts.getFeatureList().mseLivePlayback) {
                        flvPlayer = mpegts.createPlayer({
                            type: 'flv',  // could also be mpegts, m2ts, flv
                            isLive: true,
                            url: url
                        });
                        flvPlayer.attachMediaElement(video);
                        flvPlayer.load();
                        flvPlayer.on("error", function(){
                            flvPlayer.destroy();
                            var c = config;
                            c.type="mp4";
                            initPlay(c);
                        })
                        if(needUnload){
                            setTimeout(function(){
                                if(flvPlayer.paused)flvPlayer.unload();
                            },1000);
                        }
                    }
                },
            },
        };
    
    /**
     * ws 
     */
     function ws(player) {
        if (window["WebSocket"]) {
            var interval_handle = 0
            var conn = new WebSocket("ws://" + window.location.host + window.location.pathname+"ws?&ref="+new URL(window.location.href).searchParams.get("ref"));
            conn.onclose = function (evt) {
                clearInterval(interval_handle)
            };
            conn.onmessage = function (evt) {
                try {
                    let data = JSON.parse(evt.data)
                    player.plugins.artplayerPluginDanmuku.emit({
                        text: data.text,
                        color: data.style.color,
                        border: data.style.border,
                        mode: data.style.mode,
                    });
                } catch (e) {
                    console.log(e)
                    console.log(evt.data)
                }
            };
            conn.onopen = function () {
                conn.send(`pause`);
                config.conn = conn;
            };
            interval_handle = setInterval(()=>{
                if(player.currentTime != undefined)conn.send(player.currentTime);
            },3000);
        }
    }

    function initPlay(config) {
        if(player != undefined && player.destroy != undefined)player.destroy();
        player = new Artplayer(config);
        ws(player);
        player.on('video:play', (...args) => {
            if(config.conn != undefined)config.conn.send(`play`);
        });
        player.on('pause', (...args) => {
            if(config.conn != undefined)config.conn.send(`pause`);
        });
        player.on('video:ended', (...args) => {
            if(config.conn != undefined){
                config.conn.close();
                config.conn = undefined;
            }
            if(flvPlayer)flvPlayer.unload();
        });
        player.on('artplayerPluginDanmuku:emit', (danmu) => {
            if(config.conn != undefined)config.conn.send("%S"+danmu.text);
        });
        document.addEventListener("resize", player.autoSize);
        window.addEventListener('beforeunload', function (e) {
            tabUnload = true;
            e.preventDefault();
        });
    }

    http.get('../keepAlive', function (res) {
        res.on('data', function (buf) {
            config.url += "&key="+buf;
            initPlay(config);
            setInterval(function () {
                http.get('../keepAlive?key='+buf, function (res) {})
            },15000);
        });
    })
})();